import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
export default {
	data() {
		return {
			cacheSize:0
		}
	},
	components: {
		uniListItem
	},
	onLoad() {
		this.intitCacheSize();
	},
	filters:{
		format(value){
			// toFixed 保留消暑两位
			return value > 1024 ? (value/1024).toFixed(2)+ 'MB' : value.toFixed(2) + 'kb';
		}
	},
	methods: {
		//清楚缓存
		clearCache(){
			uni.showModal({
				title: '提示',
				content: '是否要清楚所有缓存',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确认',
				success: res => {
					if(res.confirm){
						uni.clearStorageSync(); //同步清楚缓存
						this.intitCacheSize();
						uni.showToast({
							title: "清楚成功",
							icon: 'none'
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		// 
		intitCacheSize(){
			 this.cacheSize = uni.getStorageInfoSync();	
		},
		
		// 注销
		logout(){
			this.$store.commit('logout');
			console.log("注销成功:"+this.$store.state.loginStatus);
			
			uni.navigateBack({
			    delta: 1
			});
		}
	}
}
